
.headers[data-v-161dda71]{
  color:#fff;
  padding:6px;
  padding-left:2%;
  font-size:14px;
  background:rgb(176,14,56);
  overflow: hidden;
}
.headers span[data-v-161dda71]:first-child{
  float:left
}
.headers span[data-v-161dda71]:last-child{
  float:right
}
.ProductT[data-v-161dda71],
.ProductN[data-v-161dda71]{
  width:100%;
  overflow: hidden;
  background:rgb(243,243,243)
}
.ProductN .ProList[data-v-161dda71]{
  padding:10px;
  float:left;
  width:48%;
  margin-left:1%;
  margin-right:1%;
  text-align: center;
  background:#fff;
    margin-bottom:10px;
}
.ProductN .ProList img[data-v-161dda71]{
  display: block;
  width:100%;
}
.ProductN .ProList  span[data-v-161dda71]{
  display: block;
  width: 100%;
  margin-top:10px;
}
.ProductN .ProList .names[data-v-161dda71]{
border-top: 1px solid #666;
padding:10px 0;
overflow: hidden;
}
.ProductN .ProList .names .left[data-v-161dda71]{
  float:left;
  color:red
}
.ProList .names .right[data-v-161dda71]{
  line-height:20px;
  float:right;
}

/*第二种样式*/
.ProductT .ProList[data-v-161dda71]{
  margin-bottom:10px;
  padding:10px;
  overflow: hidden;
  display:block;
    background:#fff;
}
.ProductT .ProList .left[data-v-161dda71]{
  width:46%;
  float:left;
}
.ProductT .ProList .left img[data-v-161dda71]{
  width:100%;
}
.ProductT .ProList .right[data-v-161dda71]{
    width:46%;
  float: right;
}
.ProductT .ProList .right span[data-v-161dda71]{
  display: block;
  overflow: hidden;
  margin-top:10px;
}
.ProductT .ProList .right span > i[data-v-161dda71]:nth-child(1){
  float:left;
  color:red;
}
.ProductT .ProList .right span > i[data-v-161dda71]:nth-child(2){
  float:right;
}



